import React, { useState } from 'react';
import './Mastermind.css';

function Mastermind()  {
    return (
        <Game />
        );
}

function Game() {
    const [secretWord, setSecretWord] = useState('')
    const [secretDisplay, setSecretDisplay] = useState('')
    const [guess, setGuess] = useState('')
    const [guesses, setGuesses] = useState([])
    const [solved, setSolved] = useState(false)
    const displayGuesses = showGuessesTable();
    const promptForGuess = showGuessPrompt();
    const promptForPlayAgain = showPlayAgainPrompt();
    return (
        <div className="Mastermind">
            <h1>Word Mastermind</h1>
            <table>
                <tr>
                    <td className="Outertable">
                    {secretWord === '' ? pickRandomWord() : ''}
                    <h2>Secret Word: {solved ? secretWord : secretDisplay}</h2>
                    {solved ?
                        promptForPlayAgain
                        :
                        promptForGuess
                    }
                    </td>
                    <td className="Outertable">
                    {guesses.length === 0 ?
                        <p>No guesses yet</p> 
                        :
                        displayGuesses
                    }
                    </td>
                </tr>
            </table>
            {solved ?
                <br></br>
                :
                guesses.length === 0 ?
                    <p>Start guessing</p>
                    :
                    <p>Keep guessing</p>
            }
        </div>
    );

    function showPlayAgainPrompt() {
        return <div>
            <h1>Solved in {guesses.length} moves!</h1>
            <button
            onClick={function () {
                pickRandomWord();
                setGuess('');
                setGuesses([]);
                setSolved(false);
            } }
            >
                Play Again!
            </button>
            </div>;
    }

    function showGuessPrompt() {
        return <div className="form-group">
            {guesses.length === 0 ?
                <label>First guess:</label>
                :
                <label>Next guess:</label>
            }
            <input className="form-control"
                name="guess"
                value={guess}
                onChange={(e) => {
                    if (e.target.value.length === secretWord.length) {
                        setGuesses([...guesses, e.target.value.toUpperCase()]);
                        setGuess('');
                        setSolved(e.target.value.toUpperCase() === secretWord);
                    } else {
                        setGuess(e.target.value.toUpperCase());
                    }
                } } />
        </div>;
    }

    function showGuessesTable() {
        return <table className="Guesstable">
            <thead>
                <tr>
                    <th>Guess</th>
                    <th>Correct Letter</th>
                    <th>Correct Position</th>
                </tr>
            </thead>
            <tbody>
                {guesses.map((g) => (
                    <tr>
                        <td>{g}</td>
                        <td>{calcCorrectLetterCount(g)}</td>
                        <td>{calcCorrectPositionCount(g)}</td>
                    </tr>
                )
                )}
            </tbody>
        </table>;
    }

    function pickRandomWord() {
        const newlen = secretWord ? secretWord.length < 2 ? 2 : secretWord.length > 7 ? 2 : secretWord.length + 1 : 2;
        const url = 'https://words-scrabble.herokuapp.com/api/random/' + newlen.toString();
        fetch(url).then(res => res.text()).then(text => {
            setSecretWord(text);
            setSecretDisplay('Length ' + text.length);
        })
    }

    function calcCorrectLetterCount(guessletters) {
        let n = 0
        const alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
        for(let letter of alphabet) {
            let x = secretWord.split(letter).length - 1
            let y = guessletters.split(letter).length - 1
            n = n + (x < y ? x : y)
        }
        return n
    }

    function calcCorrectPositionCount(guessletters) {
        let n = 0
        for(var i=0; i<secretWord.length; ++i) {
            n = n + (secretWord[i] === guessletters[i] ? 1 : 0)
        }
        return n
    }
}



export default Mastermind;